* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: url("photo.11a2bbe3.jpg");
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
  font-family: inherit;
}

h1, h2, h3, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list, .country-info {
  flex-direction: column;
  gap: 20px;
  padding: 20px 60px;
  display: flex;
}

.country-list__item {
  width: 500px;
  background-color: rgba(255, 255, 255, .373);
  border-radius: 5px;
  align-items: center;
  gap: 20px;
  display: flex;
}

.country-list__img-box, .card__img-box {
  width: 120px;
  height: 100px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

.country-list__img, .card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

.card {
  background-color: rgba(255, 255, 255, .373);
  padding: 40px;
}

.card__name {
  margin-top: 50px;
}

.card__capital {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 700;
}

.card__population, .card__languages {
  margin-top: 12px;
  font-size: 18px;
  font-weight: 700;
}

